async function initVillages() {
    const list = await store.village.getAll();
    if (list.length === 0) {
        const l = [];
        l.forEach(v => store.village.set(v));
    } else if (debug) {
        console.log('Villages', JSON.stringify(list));
    }
}

async function initConfiguration() {
    store.configuration.set({
        type: 'defaultWeight',
        weight: {
            nbCellAccessible: 5,
            nbShortPath: 10,
            nbMovements: 10,
            nbComplexMove: 1,
            nbHardMove: 40,
        },
    });
}

function init() {
    initVillages();
    initHouses();
    initConfiguration();
}

export default init;


